/*TransitionLink.css*/
.transition-div {
	z-index: 1000;
	display: block;
	position: absolute;
	top: 50%;
	left: 50%;
	height: 100%;
	width: 100%;
	background-color: yellow;
	opacity: 1;
	-webkit-animation-name: transitionIn;
	        animation-name: transitionIn;
	-webkit-animation-iteration-count: 1;
	        animation-iteration-count: 1;
	-webkit-animation-timing-function: ease-in;
	        animation-timing-function: ease-in;
	-webkit-animation-duration: 0.2s;
	        animation-duration: 0.2s;
}
@-webkit-keyframes transitionIn {
	0% {
		height: 0%;
		width: 0%;
	}
	100% {
		height: 100%;
		width: 100%;
	}
}
@keyframes transitionIn {
	0% {
		height: 0%;
		width: 0%;
	}
	100% {
		height: 100%;
		width: 100%;
	}
}
/*Button.css*/
.btn {	border-radius: 1000px;	margin: 0 auto;	padding: 8px 16px;
	background-color: #0092ff;
	text-decoration: none;
	color: #fff;
	display: inline-block;
	max-width: 200px;
	min-width: 100px;
	border: 4px solid #0092ff;
	text-align: center;
	-webkit-transition: all ease 0.2s;
	transition: all ease 0.2s;
}
.btn:hover {
	background-color: #fff;
	color: #404547;
}
/*Nav.css*/
.nav {
	text-align: right;
	margin-bottom: 40px;
}
.navbar-inner-container > a {
	margin: 0 8px;
}
.navbar-inner-container > a:last-child {
	margin-right: 0;
}
/*Footer.css*/
.footer {
	height: 160px;
	display: flex;
}
.footer > div {
	margin: auto;
}
/*Page.css*/
.page {
	margin: 64px auto;
	max-width: 64rem;
	padding: 0 16px;
}
.fade-in {
	background-color: yellow;
	display: block;
	position: absolute;
	top: 50%;
	left: 50%;
	width: 0%;
	height: 0%;
	border-radius: 100%;
	/* opacity: 0; */
	-webkit-animation-name: fadeIn;
	        animation-name: fadeIn;
	-webkit-animation-iteration-count: 1;
	        animation-iteration-count: 1;
	-webkit-animation-duration: 0.2s;
	        animation-duration: 0.2s;
	-webkit-animation-timing-function: ease;
	        animation-timing-function: ease;
}
@-webkit-keyframes fadeIn {
	0% {
		height: 100%;
		width: 100%;
	}
	100% {
		height: 0%;
		width: 0%;
	}
}
@keyframes fadeIn {
	0% {
		height: 100%;
		width: 100%;
	}
	100% {
		height: 0%;
		width: 0%;
	}
}
