/*Home.css*/
.flex-div {
	width: 100%;
	display: flex;
	flex-direction: row;
}
.flex-div > div {
	max-width: 480px;
}
.img-container {
	width: 100%;
	display: flex;
}
img {
	width: 100%;
}
/*TextInput.css*/
.input-container {
	position: relative;
	width: 100%;
	height: 40px;
	display: flex;
}
.input-icon {
	height: 40px;
	width: 40px;
	display: flex;
	z-index: 100;
	position: absolute;
}
.input-icon > img {
	max-width: 20px;
	margin: auto;
}
input {
	height: 100%;
	margin: auto 0;
	width: 100%;
	border: 4px solid #0092ff;
	border-radius: 1000px;
	padding-left: 32px;
	font-family: 'Open Sans', sans-serif;
	font-size: 14px;
}
input:active {
	outline: 1px solid #0092ff;
}
/*RecipeCard.css*/
.recipe-card {
	display: grid;
	grid-template-rows: 80px 16px auto 8px auto 16px auto 16px auto;
	padding: 16px;
	min-width: 300px;
	background-color: #fff;
	-webkit-transition: all ease 0.2s;
	transition: all ease 0.2s;
}
.recipe-card > div:first-child {
    overflow: hidden;
}
.recipe-card > div:first-child > img {
	margin-top: -16px;
}

.recipe-card > div:last-child {
    text-align: right;
}
.recipe-card:hover {
	box-shadow: 0 8px 12px 4px #7f8c8d;
}
/*GetTacos.css*/
.recipes-container {
	display: grid;
	grid-template-columns: auto auto auto;
	grid-gap: 16px;
}
div[role="banner"] {
	text-align: center;
}
div[role="banner"] > div:first-child {
	margin-bottom: 40px;
}
div[role="banner"] > div:last-child {
	display: flex;
	justify-content: space-around;
	max-width: 20rem;
	margin: auto;
}
div[role="banner"] > div:last-child > span {
	height: 24px;
	width: 4px;
	background-color: #0092ff;
	display: block;
	border-radius: 1000px;
}
div[role="banner"] > div:last-child > div:last-child {
	
}

/*media queries*/
@media only screen and (max-width: 980px) {
	.recipes-container {
		grid-template-columns: auto auto;
	}
}
@media only screen and (max-width: 640px) {
	.recipes-container {
		grid-template-columns: auto;
	}
}
/*Ingredient.css*/
.ingredient {
	display: inline-block;
    padding: 4px 8px;
    background-color: #404547;
	font-size: 18px;
	font-family: 'Open Sans', sans-serif;
	color: #fff;
	border-radius: 2px;
}
.ingredient > span:first-child {

    margin-right: 8px;
    border-right: 1px solid white;
    padding-right: 8px;
    border-radius: 2px;
	font-weight: 500;
}
.ingredient > span:last-child {
	font-weight: 100;
}
