* {
	box-sizing: border-box;
}
html, body {
	width: 100%;
	height: 100vh;
	margin: 0;
	padding: 0;
	font-family: 'Montserrat', 'Lato', 'Helvetica', sans-serif;
	color: #404547;
	background-color: #f2f1ef;
}
h1, h2, h3, h4, h5, h6, p {
	margin: 0;
	padding: 0;
}
h1 {
	font-size: 48px;
}
h2 {
	font-size: 32px;
}
h3 {
	font-size: 24px;
}
h4 {
	font-size: 20px;
}
p {
	font-family: 'Open Sans', sans-serif;
	font-size: 18px;
}
p.sm {
	font-size: 14px;
}
.muted {
	font-style: italic;
	font-weight: 100;
	opacity: 0.8;	
}
.light {
	font-weight: 100;
}
.box-shadow {
	box-shadow: 0 2px 6px -1px #7f8c8d;
}
li {
	font-family: 'Open Sans', sans-serif;
}
ul > li {
	margin-bottom: 16px;
}
ol > li {
	position: relative;
	list-style: none;
	margin: 0 0 24px -6px;
}
ol > li:before {
	position: absolute;
	left: -34px;
	content: attr(data-number);
	display: inline-block;
	border-radius: 100%;
	height: 22px;
	width: 24px;
	background-color: #0092ff;
	color: #fff;
	text-align: center;
	padding-top: 1px;
	font-family: 'Open Sans', sans-serif;
	font-size: 14px;
	font-weight: 800;
}
li.complete {
    text-decoration: line-through;
    opacity: 0.7;
}
li.complete:before {
    background-color: #404547;
}
